import React, { useContext } from "react";
import { styled, Box, Typography,} from "@mui/material";
import { Delete } from "@mui/icons-material";
import { handleDelete } from "../Fetch/FetchData";
import AuthContext from "../Context";

const ContextMenu = ({ x, y, id }) => {

  const { imageUpdated } = useContext(AuthContext);
  return (
    <ContextContainer x={x + "px"} y={y + "px"}>
      <ContextMenuItem
        onClick={() => handleDelete(id, imageUpdated)}
        sx={{
          "&:hover": {
            backgroundColor: "#E4E8F3",
          },
        }}
      >
        <Delete fontSize="small" />
        <Typography>Delete </Typography>
      </ContextMenuItem>      
    </ContextContainer>
  );
};

export default ContextMenu;

const ContextContainer = styled(Box)`
  position: absolute;
  width: 150px;
  height: fit-content;
  z-index: 3;
  background-color: #ffffff;
  box-shadow: rgba(50, 50, 93, 0.25) 0px 2px 5px -1px,
    rgba(0, 0, 0, 0.3) 0px 1px 3px -1px;
  border: 1px solid #f5f7fa;
  color: #0d1017;
  font-size: 18px;
  border-radius: 8px;
  cursor: pointer;
  left: ${(props) => props.x};
  top: ${(props) => props.y};
`;

const ContextMenuItem = styled(Box)`
  display: flex;
  align-items: center;
  gap:10px;
  padding: 10px 5px;
`;
