import React, { useContext, useState } from "react";
import {
  Button,
  Slide,
  FormControlLabel,
  Switch,
  TextField,
  Typography,
  Box,
} from "@mui/material";
import { Edit, DocumentScanner } from "@mui/icons-material";
import { Dialog, DialogContent, DialogTitle } from "@mui/material";
import { useForm } from "react-hook-form";
import AuthContext from "../Context";
import { UpdateProperty } from "../Fetch/FetchData";

const Transition = React.forwardRef(function Transition(props, ref) {
  return <Slide direction="up" ref={ref} {...props} />;
});

const style = {
  width: { sm: "200", md: "200", xs: "100" },
  "& .MuiInputBase-root": {},
};

export default function UpdateBox({ data }) {
  const { Updated } = useContext(AuthContext);
  const {
    register,
    handleSubmit,
    formState: { errors },
  } = useForm();

  function onSubmit(res, e) {
    e.preventDefault();
    console.log(data);
    UpdateProperty(data.id, res, Updated);
    console.log(data.is_tenant_active);
    handleClose();
  }

  const [open, setOpen] = useState(false);
  const handleOpen = () => {
    setOpen(true);
  };

  const handleClose = () => {
    setOpen(false);
  };

  return (
    <div>
      <Button
        onClick={handleOpen}
        startIcon={<Edit sx={{ color: "white" }}  fontSize='small' />}
        style={{
          fontFamily: "Poppins, san-serif",
          color: "white",
          fontWeight: "600",
          backgroundColor: "#537FE7",
          textAlign: "center",
          width: { xs: "80px", sm: '10rem' },
          height: { xs: "10px", lg: "40px" },
          fontSize: { xs: "10px", lg: "13px" }
        }}
      >
        Update
      </Button>
      <Dialog open={open} TransitionComponent={Transition} fullScreen>
        <DialogTitle
          sx={{
            fontFamily: "poppins",
            color: "#537FE7",
            fontWeight: "bold",
            letterSpacing: "1px",
          }}
        >
          Update Details
        </DialogTitle>
        <DialogContent style={{ textAlign: "center" }}>
          <Box
            component="form"
            sx={{
              "& .MuiTextField-root": {
                m: 1,
                display: "flex",
                flexDirection: "column",
                maxHeight: "90%",
              },
            }}
            noValidate
            autoComplete="off"
            onSubmit={handleSubmit(onSubmit)}
          >
            <div style={{ display: "flex", justifyContent: "space-between" }}>
              <Typography>Property Details</Typography>
              <FormControlLabel
                control={
                  <Switch
                    {...register("is_property_active")}
                    defaultChecked={data.is_property_active}
                  />
                }
                label="Active Property"
              />
            </div>
            <TextField
              sx={style}
              label="Name of Property"
              size="small"
              error={errors.property_name ? true : false}
              required
              {...register("property_name", {
                required: true,
              })}
              defaultValue={data.property_name}
              padding="100px"
            />
            <div
              style={{
                display: "flex",
                justifyContent: "space-between",
              }}
            >
              <TextField
                style={{ width: "50%" }}
                sx={style}
                size="small"
                label="Address"
                required
                type="text"
                multiline
                rows={3}
                placeholder="Address"
                error={errors.address ? true : false}
                {...register("address", {
                  required: true,
                })}
                defaultValue={data.address}
              />
              <div style={{ width: "50%" }}>
                <TextField
                  sx={style}
                  label="BHK"
                  type="number"
                  size="small"
                  error={errors.bhk ? true : false}
                  required
                  {...register("bhk", {
                    min: 1,
                    required: true,
                  })}
                  defaultValue={data.bhk}
                />
                <TextField
                  sx={style}
                  label="Rent"
                  type="number"
                  size="small"
                  required
                  error={errors.rent ? true : false}
                  {...register("rent", {
                    min: 1,
                    required: true,
                  })}
                  defaultValue={data.rent}
                />
              </div>
            </div>
            <div style={{ display: "flex", justifyContent: "space-between" }}>
              <Typography>Tenant Detail</Typography>

              <FormControlLabel
                control={<Switch {...register("is_tenant_active")} defaultChecked={data.is_tenant_active} />}
                label="Active Tenant"
              />
            </div>
            <TextField
              sx={style}
              label="Name of Tenant"
              size="small"
              error={errors.tenant_name ? true : false}
              {...register("tenant_name")}
              defaultValue={data.tenant_name}
            />
            <div
              style={{
                width: "100%",
                display: "flex",
                justifyContent: "space-between",
              }}
            >
              <TextField
                sx={style}
                style={{ width: "50%" }}
                label="email"
                size="small"
                {...register("email", {
                  pattern: /^[^@ ]+@[^@ ]+\.[^@ .]{2,}$/,
                })}
                defaultValue={data.email}
              />
              <TextField
                sx={style}
                style={{ width: "50%" }}
                label="Age"
                type="number"
                size="small"
                error={errors.age ? true : false}
                {...register("age")}
                defaultValue={data.age}
              />
            </div>
            <div
              style={{
                width: "100%",
                display: "flex",
                justifyContent: "space-between",
              }}
            >
              <TextField
                sx={style}
                style={{ width: "50%" }}
                label="Phone Number"
                type="number"
                size="small"
                error={errors.phone_number ? true : false}
                {...register("phone_number", {
                  minLength: 10,
                  maxLength: 10,
                })}
                defaultValue={data.phone_number}
              />
              <TextField
                sx={style}
                style={{ width: "50%" }}
                label="AADHAR Number"
                type="Number"
                size="small"
                error={errors.adhar_num ? true : false}
                {...register("adhar_num", {
                  minLength: 12,
                  maxLength: 12,
                })}
                defaultValue={data.adhar_num}
              />
            </div>
            <div style={{ display: "flex", justifyContent: "space-around" }}>
              <TextField
                style={{ width: "97%" }}
                sx={style}
                label="Rent Date"
                InputLabelProps={{ shrink: true }}
                type="date"
                size="small"
                error={errors.rent_date ? true : false}
                {...register("rent_date")}
                defaultValue={data.rent_date}
              />
              <TextField
                style={{ width: "97%" }}
                sx={style}
                label="Payment Date"
                type="Number"
                size="small"
                error={errors.rent_due_date ? true : false}
                {...register("rent_due_date", {
                  min: 1,
                  max: 10,
                })}
                defaultValue={data.rent_due_date}
              />
            </div>
            <div className="upload">
              <Button
                startIcon={<DocumentScanner />}
                component="label"
              >
                Aadhar image{" "}
                <input
                  type="file"
                  accept="image/*"
                  hidden
                  {...register("adhar_pic")}
                />
              </Button>
            </div>
            <div style={{ display: "flex", justifyContent: "space-around" }}>
              <Button color="error" variant="contained" onClick={handleClose}>
                Close
              </Button>
              <Button color="success" variant="contained" type="submit">
                Update
              </Button>
            </div>
          </Box>
        </DialogContent>
      </Dialog>
    </div>
  );
}
