import React, { useContext } from "react";
import Box from "@mui/material/Box";
import { TextField, Button, Typography,FormControlLabel,Switch } from "@mui/material";
import { useForm } from "react-hook-form";
import { DocumentScanner } from "@mui/icons-material";
import { inputProperty } from "../Fetch/FetchData";
import AuthContext from "../Context";
const style = {
  width: { sm: "200", md: "200", xs: "100" },
  "& .MuiInputBase-root": {
    // height: '80'
  },
};

export default function Form(props) {
  const { Added } = useContext(AuthContext);
  const {
    register,
    handleSubmit,
    formState: { errors },
  } = useForm();

  function onSubmit(data, e) {
    e.preventDefault();
    inputProperty(data, Added);
    props.onClose();
  }

  return (
    <Box
      component="form"
      sx={{
        "& .MuiTextField-root": {
          m: 1,
          display: "flex",
          flexDirection: "column",
          maxHeight: "90%",
        },
      }}
      noValidate
      autoComplete="off"
      onSubmit={handleSubmit(onSubmit)}
    >
      <div style={{display:'flex',justifyContent:'space-between'}}>
        <Typography>
          Property Details
        </Typography>
        <FormControlLabel
            control={
              <Switch
                {...register("is_property_active")}
              />
            }
            label="Active Property"
          />
      </div>
      <TextField
        sx={style}
        label="Name of Property"
        size="small"
        error={errors.property_name ? true : false}
        required
        {...register("property_name", {
          required: true,
        })}
        padding="100px"
      />
      <div
        style={{
          display: "flex",
          justifyContent: "space-between",
        }}
      >
        <TextField
          style={{ width: "50%" }}
          sx={style}
          size="small"
          label="Address"
          required
          type="text"
          multiline
          rows={3}
          placeholder="Address"
          error={errors.address ? true : false}
          {...register("address", {
            required: true,
          })}
        />
        <div style={{ width: "50%" }}>
          <TextField
            sx={style}
            label="BHK"
            type="number"
            size="small"
            error={errors.bhk ? true : false}
            required
            {...register("bhk", {
              min: 1,
              required: true,
            })}
          />
          <TextField
            sx={style}
            label="Rent"
            type="number"
            size="small"
            required
            error={errors.rent ? true : false}
            {...register("rent", {
              min: 1,
              required: true,
            })}
          />
        </div>
      </div>
      <Button
        startIcon={<DocumentScanner />}
        component="label"
        color={errors.uploaded_images ? "error" : "primary"}
      >
        property image{" "}
        <input
          type="file"
          accept="image/*"
          hidden
          multiple
          {...register("uploaded_images", {
            required: true,
          })}
        />
      </Button>
      <div style={{display:'flex',justifyContent:'space-between'}}>
        <Typography>Tenant Detail</Typography>
        
        <FormControlLabel
            control={
              <Switch
                {...register("is_tenant_active")}
              />
            }
            label="Active Tenant"
          />
      </div>
      <TextField
        sx={style}
        label="Name of Tenant"
        size="small"
        error={errors.tenant_name ? true : false}
        {...register("tenant_name")}
      />
      <div
        style={{
          width: "100%",
          display: "flex",
          justifyContent: "space-between",
        }}
      >
        <TextField
          sx={style}
          style={{ width: "50%" }}
          label="email"
          size="small"
          {...register("email", { pattern: /^[^@ ]+@[^@ ]+\.[^@ .]{2,}$/ })}
        />
        <TextField
          sx={style}
          style={{ width: "50%" }}
          label="Age"
          type="number"
          size="small"
          error={errors.age ? true : false}
          {...register("age")}
        />
      </div>
      <div
        style={{
          width: "100%",
          display: "flex",
          justifyContent: "space-between",
        }}
      >
        <TextField
          sx={style}
          style={{ width: "50%" }}
          label="Phone Number"
          type="number"
          size="small"
          error={errors.phone_number ? true : false}
          {...register("phone_number", {
            minLength: 10,
            maxLength: 10,
          })}
        />
        <TextField
          sx={style}
          style={{ width: "50%" }}
          label="AADHAR Number"
          type="Number"
          size="small"
          error={errors.adhar_num ? true : false}
          {...register("adhar_num", {
            minLength: 12,
            maxLength: 12,
          })}
        />
      </div>
      <div style={{ display: "flex", justifyContent: "space-around" }}>
        <TextField
          style={{ width: "97%" }}
          sx={style}
          label="Rent Date"
          InputLabelProps={{ shrink: true }}
          type="date"
          size="small"
          error={errors.rent_date ? true : false}
          {...register("rent_date")}
        />
        <TextField
          style={{ width: "97%" }}
          sx={style}
          label="Payment Date"
          type="Number"
          size="small"
          error={errors.rent_due_date ? true : false}
          {...register("rent_due_date", {
            min: 1,
            max: 10,
          })}
        />
      </div>
      <div className="upload">
        <Button
          startIcon={<DocumentScanner />}
          component="label"
          color={errors.adhar_pic && "error"}
        >
          Aadhar image{" "}
          <input
            type="file"
            accept="image/*"
            hidden
            {...register("adhar_pic")}
          />
        </Button>
      </div>
      <div style={{ display: "flex", justifyContent: "space-around" }}>
        <Button color="error" variant="contained" onClick={props.onClose}>
          Close
        </Button>
        <Button color="success" variant="contained" type="submit">
          Add
        </Button>
      </div>
    </Box>
  );
}
