import React, { useState,useContext } from "react";
import { Button, Slide,Typography } from "@mui/material";
import { Delete,Add} from "@mui/icons-material";
import { Dialog, DialogContent, DialogTitle } from "@mui/material";
import AuthContext from "../Context";
import { handleAdd } from "../Fetch/FetchData";
import { useForm } from "react-hook-form";
const Transition = React.forwardRef(function Transition(props, ref) {
  return <Slide direction="up" ref={ref} {...props} />;
});

export default function AddImages({id}) {
    const {Updated}=useContext(AuthContext)
    const {register,handleSubmit}=useForm()
  const [open, setOpen] = useState(false);
  const handleOpen = () => {
    setOpen(true);
  };

  const handleClose = () => {
    setOpen(false);
  };
  
  function onSubmit(data){
    handleAdd(id,data,Updated)
    handleClose()
  }
  return (
    <div>
      <Button
        onClick={handleOpen}
        startIcon={<Add sx={{ color: "white" }} fontSize='small' />}
        style={{
          fontFamily: "Poppins, san-serif",
          color: "white",
          fontWeight: "600",
          backgroundColor:'#537FE7',
          textAlign:'center',
          width: { xs: "80px", lg: '10rem' },
          height: { xs: "10px", lg: "40px" },
          fontSize: { xs: "9px", lg: "13px" }
        }}
      >
         Images
      </Button>
      <Dialog
        open={open}
        TransitionComponent={Transition}
        onClose={handleClose}
      >
        <DialogTitle>Add Images</DialogTitle>
        <DialogContent style={{ textAlign: "center" }}>
          <Typography variant="subtitle2" gutterBottom> Select Images You want to Upload</Typography>
          <form onSubmit={handleSubmit(onSubmit)} style={{display:'flex',flexDirection:'column'}}>
            <Button startIcon={<Add />} component="label" style={{margin:'10px'}}>
              Select Images{" "}
              <input
                type="file"
                accept="image/*"
                hidden
                multiple
                {...register('uploaded_images')}
                // onChange={handleChange}
              />
            </Button>
            <Button type="submit" variant="contained">
              Upload
            </Button>
          </form>
        </DialogContent>
      </Dialog>
    </div>
  );
}
