import React,{useContext, useEffect, useState} from 'react';
import { useTheme } from '@mui/material/styles';
import Box from '@mui/material/Box';
import MobileStepper from '@mui/material/MobileStepper';
import Button from '@mui/material/Button';
import KeyboardArrowLeft from '@mui/icons-material/KeyboardArrowLeft';
import KeyboardArrowRight from '@mui/icons-material/KeyboardArrowRight';
import SwipeableViews from 'react-swipeable-views';
import { autoPlay } from 'react-swipeable-views-utils';
import ContextMenu from './ContextMenu';
import AuthContext from '../Context';


const AutoPlaySwipeableViews = autoPlay(SwipeableViews);


function Carousel({images}) {
  const [toggleContextMenu, setToggleContextMenu] = useState(false);
  const [itemId, setItemId] = useState(null);
  const [itemPosition, setItemPosition] = useState({ x: 0, y: 0 });

  const onRightClick = (e, id) => {
    e.preventDefault();
    let parentW = document.getElementById("image").offsetWidth;

    let x = e.clientX,
      y = e.clientY;

    let leftMarginOffset = (1 / 100) * parentW;

    if (window.innerWidth - e.clientX < 160 + leftMarginOffset) {
      x = window.innerWidth - (leftMarginOffset + 160);
    }

    if (window.innerHeight - e.clientY < 60) {
      y = window.innerHeight - 65;
    }

    var coordinate = { ...itemPosition };
    coordinate.x = x;
    coordinate.y = y;

    setItemPosition(coordinate);
    setToggleContextMenu(true);
    setItemId(id);
  };

  const theme = useTheme();
  const [activeStep, setActiveStep] = useState(0);
  const maxSteps = images?.length;
  const handleNext = () => {
    setActiveStep((prevActiveStep) => prevActiveStep + 1);
  };

  const handleBack = () => {
    setActiveStep((prevActiveStep) => prevActiveStep - 1);
  };

  const handleStepChange = (step) => {
    setActiveStep(step);
  };

  return (
    <Box sx={{ maxWidth: 700, flexGrow: 1 }} onClick={()=>setToggleContextMenu(false)}>
      <AutoPlaySwipeableViews
        axis={theme.direction === 'rtl' ? 'x-reverse' : 'x'}
        index={activeStep}
        onChangeIndex={handleStepChange}
        enableMouseEvents
      >
        {images?.map((step, index) => (
          <div key={step.label}>
            {Math.abs(activeStep - index) <= 2 ? (
              <Box
                component="img"
                id='image'
                key={step.id}
                onContextMenu={(e)=>onRightClick(e,step.id)}
                sx={{
                  height: {xs:200,sm:400},
                  display: 'block',
                  maxWidth: 700,
                  overflow: 'hidden',
                  width: '100%',
                }}
                src={step.property_pic}
              />
            ) : null}
          </div>
        ))}
      </AutoPlaySwipeableViews>
        {toggleContextMenu && <ContextMenu id={itemId} x={itemPosition.x} y={itemPosition.y}/>}
      <MobileStepper
        steps={maxSteps}
        position="static"
        activeStep={activeStep}
        nextButton={
          <Button
            size="small"
            onClick={handleNext}
            disabled={activeStep === maxSteps - 1}
          >
            Next
            {theme.direction === 'rtl' ? (
              <KeyboardArrowLeft />
            ) : (
              <KeyboardArrowRight />
            )}
          </Button>
        }
        backButton={
          <Button size="small" onClick={handleBack} disabled={activeStep === 0}>
            {theme.direction === 'rtl' ? (
              <KeyboardArrowRight />
            ) : (
              <KeyboardArrowLeft />
            )}
            Back
          </Button>
        }
      />
    </Box>
  );
}

export default Carousel;