//For Cookie
export function getCookie(name) {
  let cookieValue = null;
  if (document.cookie && document.cookie !== "") {
    const cookies = document.cookie.split(";");
    for (let i = 0; i < cookies.length; i++) {
      const cookie = cookies[i].trim();
      if (cookie.startsWith(`${name}=`)) {
        cookieValue = decodeURIComponent(cookie.substring(name.length + 1));
        break;
      }
    }
  }
  return cookieValue;
}

//Login
export const UserLogin = async (Login, data) => {
  try {
    const csrfToken = getCookie("csrftoken");
    const response = await fetch(Login_URL, {
      method: "POST",
      headers: {
        "Content-Type": "application/json",
        "X-CSRFToken": csrfToken,
      },
      body: JSON.stringify(data),
    });
    if (response.ok) {
      const jsonData = await response.json();
      Login();
      let token = jsonData.Token.access;
      let refresh_token = jsonData.Token.refresh;
      localStorage.setItem("token", token);
      localStorage.setItem("refresh_token", refresh_token);
      console.log("loggedIN");
    } else {
      alert("Wrong username or password");
      console.log("Invalid Email or password");
    }
  } catch (error) {
    console.log(error.message);
  }
};

//Add Property
export const inputProperty = async (data, Added) => {
  const formData = new FormData();

  formData.append("property_name", data.property_name);
  formData.append("tenant_name", data.tenant_name);
  formData.append("age", data.age);
  formData.append("rent", data.rent);
  formData.append("rent_date", data.rent_date);
  formData.append("rent_due_date", data.rent_due_date);
  formData.append("address", data.address);
  formData.append("email", data.email);
  formData.append("bhk", data.bhk);
  formData.append("phone_number", data.phone_number);
  formData.append("adhar_num", data.adhar_num);
  formData.append("is_tenant_active", data.is_tenant_active);
  formData.append("is_property_active", data.is_property_active);
  if (data.adhar_pic[0] !== undefined) formData.append("adhar_pic", data.adhar_pic[0]);
  const files = [...data.uploaded_images];
  files.forEach((file) => {
    formData.append("uploaded_images", file);
  });

  try {
    const csrfToken = getCookie("csrftoken");
    const token = localStorage.getItem("token");
    const response = await fetch(post_URL, {
      method: "POST",
      headers: {
        Authorization: `Bearer ${token}`, // include token in Authorization header
        "X-CSRFToken": csrfToken,
      },
      body: formData,
    });
    if (response.ok) {
      console.log("Property added successfully");
      Added();
    } else {
      const error = await response.json();
      console.log(error);
    }
  } catch (error) {
    console.log(error);
  }
};

