//Get Excel File
export const fetchExcel = async () => {
  try {
    const csrfToken = getCookie("csrftoken");
    const token = localStorage.getItem("token");
    const response = await fetch(export_URL, {
      method: "GET",
      headers: {
        Authorization: `Bearer ${token}`,
        "X-CSRFToken": csrfToken,
      },
      responseType: "blob",
    });
    if (response.ok) {
      const blob = await response.blob();
      const url = window.URL.createObjectURL(blob);
      const link = document.createElement("a");
      link.href = url;
      link.download = "tenants.xlsx";
      link.click();
    }
  } catch (error) {
    console.log(error);
  }
};

//Logout
export const UserLogout = async (Logout) => {
  try {
    const token = localStorage.getItem("token"); // get token from localStorage
    const response = await fetch(Logout_URL, {
      method: "GET",
      headers: {
        "Content-Type": "application/json",
        Authorization: `Bearer ${token}`, // include token in Authorization header
      },
    });
    if (response.ok) {
      Logout();
      localStorage.removeItem("token"); // remove token from localStorage
      localStorage.removeItem("refresh_token"); // remove refresh_token from localStorage
      console.log("Logged out successfully");
    } else {
      console.log("Logout failed");
    }
  } catch (error) {
    console.log(error.message);
  }
};

//Change Password
export const changePassword = async (data, Logout) => {
  try {
    const csrfToken = getCookie("csrftoken");
    const token = localStorage.getItem("token");
    const response = await fetch(changePassword_URL, {
      method: "POST",
      headers: {
        "Content-Type": "application/json",
        Authorization: `Bearer ${token}`,
        "X-CSRFToken": csrfToken,
      },
      body: JSON.stringify(data),
    });
    if (response.status === 204) {
      console.log("Password changed successfully.");
      UserLogout(Logout);
    } else {
      const errorData = await response.json();
      console.log(errorData);
    }
  } catch (error) {
    console.log(error);
  }
};

//Token Refresh
export const tokenRefresh = async () => {
  const refresh_token = localStorage.getItem("refresh_token");
  try {
    const response = await fetch(refresh_URL, {
      method: "POST",
      headers: {
        "Content-Type": "application/json",
        Authorization: `Bearer ${localStorage.getItem("refresh_token")}`,
      },
      body: JSON.stringify({
        refresh: refresh_token,
      }),
    });
    const jsonData = await response.json();
    const access = jsonData.access;
    if (access != null) {
      localStorage.setItem("token", access);
    }
  } catch (error) {
    console.log(error);
  }
};

