import { Visibility, VisibilityOff } from "@mui/icons-material";
import {
  Button,
  FormControl,
  TextField,
  InputAdornment,
  Typography,
  IconButton,
  Box,
  Grid,
} from "@mui/material";
import { Container } from "@mui/system";
import { useForm } from "react-hook-form";
import React, { useState, useContext,useEffect } from "react";
import { useNavigate } from "react-router-dom";
import { UserLogin, getCookie, googleAuth } from "../../Fetch/FetchData";
import AuthContext from "../../Context";
import Header from "../../components/Header/Header";
import {useGoogleLogin} from '@react-oauth/google'
import axios from 'axios'
const Login = () => {
  const { Login } = useContext(AuthContext);
  const [showPassword, setShowPassword] = useState(false);
  function handleClickShowPassword() {
    setShowPassword((prev) => !prev);
  }
  const navigate = useNavigate();
  const {
    register,
    handleSubmit,
    formState: { errors },
  } = useForm();

  const onSubmit = async (data) => {
    UserLogin(Login, data);
  };

  const googleLogin = useGoogleLogin ({
    onSuccess: async (tokenResponse) => {
      console.log(tokenResponse);
      const userInfo = await axios.get(
        'https://www.googleapis.com/oauth2/v3/userinfo',
        { headers: { Authorization: `Bearer ${tokenResponse.access_token}` } },
      );
      console.log(tokenResponse.access_token)
      googleAuth(userInfo.data,tokenResponse.access_token)
    },
    onError: errorResponse => console.log(errorResponse),
  });
  return (
    <>
      <Header />
      <Container
        maxWidth="md"
        style={{
          display: "flex",
          justifyContent: "center",
          alignItems: "center",
          flexDirection: "column",
        }}
      >
        <Box
          mt={4}
          mb={4}
          sx={{
            marginTop: 4,
            display: "flex",
            flexDirection: "column",
            alignItems: "center",
            width: "360px",
          }}
        >
          <Button
            sx={{ mt: 3, mb: 2, padding: 0 }}
            type="submit"
            style={{
              width: "70%",
              backgroundColor: "",
            }}
            onClick={() => googleLogin()}
          >
            <img
              src="https://i.stack.imgur.com/LuLcN.png"
              alt="Google Login"
              style={{ width: "100%" }}
            />
          </Button>

          <Typography
            align="center"
            gutterBottom
            marginTop="5%"
            variant="h6"
            style={{
              fontWeight: "600",
              fontFamily: "Arial ",
            }}
          >
            Login
          </Typography>
          <form onSubmit={handleSubmit(onSubmit)} style={{ width: "100%" }}>
            <FormControl style={{ width: "100%" }}>
              <TextField
                placeholder="Email"
                {...register("email", {
                  required: true,
                  pattern: /^[^@ ]+@[^@ ]+\.[^@ .]{2,}$/,
                })}
                error={errors.email ? true : false}
                helperText={errors.email && "Enter Valid Email"}
                style={{ paddingBottom: "16px" }}
              />
              <TextField
                type={showPassword ? "text" : "password"}
                required
                InputProps={{
                  endAdornment: (
                    <InputAdornment position="end">
                      <IconButton onClick={handleClickShowPassword} edge="end">
                        {showPassword ? <VisibilityOff /> : <Visibility />}
                      </IconButton>
                    </InputAdornment>
                  ),
                  placeholder: "Password",
                }}
                {...register("password", { required: true, minLength: 6 })}
                error={errors.password ? true : false}
                helperText={
                  errors.password && "Password length should be more than 5"
                }
              />
              <Button variant="contained" sx={{ mt: 3, mb: 2 }} type="submit">
                Login
              </Button>
            </FormControl>
          </form>
          <Grid
            container
            style={{
              display: "flex",
              flexDirection: "column",
              justifyContent: "center",
              alignItems: "center",
              gap: "10px",
              margin: "10px",
            }}
          >
            <Grid
              item
              variant="body2"
              style={{
                fontSize: "15px",
                fontWeight: "600",
                fontFamily: "Helvetica",
              }}
            >
              Don't have an account?{" "}
              <span
                onClick={() => {
                  navigate("/signup");
                }}
                style={{
                  color: "#537FE7",
                  cursor: "pointer",
                  textDecoration: "underline",
                }}
              >
                Sign Up
              </span>
            </Grid>
          </Grid>
          <Box
            sx={{
              display: "flex",
              alignItems: "center",
            }}
          ></Box>
        </Box>
      </Container>
    </>
  );
};

export default Login;
